
.imgDiv{
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(214, 222, 228);
}
.imgDiv img{
  width: 10%;
}

.green{
  width: 100%;
  height: 60vh;
  background-color: green;
}

.leftRight{
  display: flex;
  align-items: center;
  justify-content: center;
}

.left{
  background-color: orange;
  width: 70%;
  display: flex;
  flex-wrap: wrap;
}
.left img{
  width: 30%;
}

.right{
  width: 30%;

}
.right img{
  width: 50%
}


@media screen and (max-width: 700px) {
  .leftRight{
    display: grid
  }
  .left, .right{
    width: 100%;
  }
}